import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Aoo from '@/components/aoo'
import Boo from '@/components/boo'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
    },{
		path: '/aoo',
		name: 'Aoo',
		component: Aoo,
		children:[{
			path: '/aoo/boo',
		name: 'Boo',
		component: Boo,	
		}]
	}
  ]
})



