<template>
  <div class="hello">
    <p>我就是boo,你是从aoo进来的，所以我保留了aoo的信息</p>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted(){
	document.title='我是boo页面的title'
  }
}
</script>

